.App {
  height: 100vh;
  width: 100vw;
  margin: 0;
  font-family: "Mulish", sans-serif;
  box-sizing: border-box;
}

.fakeheader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 40px;
  box-sizing: border-box;
  margin: 0;
}

.logo {
  max-width: 90px;
}

.bottom {
  margin: 0;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  background-image: linear-gradient(135deg, #010203 10%, #002b82 100%);
  align-items: center;
  justify-content: center;
  padding: 50px 40px;
  color: white;

  @media (min-width: 860px) {
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
  }
}
.content {
  display: flex;
  align-items: baseline;
  flex-direction: column;
  box-sizing: border-box;
  margin: 0;
  @media (min-width: 860px) {
    max-width: 400px;
    padding-top: 20px;
  }
}

.textbig {
  font-size: 22px;
  margin-bottom: 40px;
  @media (min-width: 860px) {
    font-size: 36px;
  }
}

.textsmall {
  font-size: 16px;
  margin-bottom: 40px;
  @media (min-width: 860px) {
    font-size: 20px;
  }
}

.actionwhite {
  background-color: white;
  margin-bottom: 40px;
  font-weight: 800;
  font-size: 20px;
  line-height: 20px;
  border-radius: 30px;
  padding: 20px 30px;
  cursor: pointer;
  border: none;
  outline: inherit;
}

.actionblack {
  background-color: black;
  color: white;
  font-weight: 800;
  font-size: 16px;
  line-height: 20px;
  border-radius: 24px;
  padding: 10px 20px;
  cursor: pointer;
  border: none;
  outline: inherit;
}

.actionblack:hover {
  opacity: 0.5;
}

.actionwhite:hover {
  opacity: 0.5;
}

.phoneImg {
  max-width: 200px;
}
